video {
  object-fit: cover;
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: -1;
}
.player-wrapper {
  position: relative;
  width: 1200px;
  height: 50%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
  flex-wrap: nowrap;
  border-radius: 5px;
  box-shadow: 0 4px 5px 0 rgb(0 0 0 / 20%), 0 4px 6px 0 rgb(0 0 0 / 19%);
  border: rgb(194, 194, 194) 1px solid;
}

.react-player {
  top: 0;
  left: 0;
}
.khungsl-container {
  /* background: url('/images/img-home.jpg') center center/cover no-repeat; */
  height: 50vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2);
  object-fit: contain;
}

.khungsl-container > h1 {
  color: #fff;
  font-size: 60px;
  margin-top: -50px;
}

.khungsl-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 32px;
   font-family: Tahoma , sans-serif;

}

.khungsl-btns {
  margin-top: 32px;
}

.khungsl-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .khungsl-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .khungsl-container > h1 {
    font-size: 50px;
    margin-top: -100px;
  }

  .khungsl-container > p {
    font-size: 30px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}
