:root {
  --primary: #fff;
  --yellow: #rgb(255 160 0);

}

.btn {
  padding: 8px 20px;
  border-radius: 2px;
  outline: none;
  border: none;
  cursor: pointer;
  font-size: 18px;
  color: #fff;
  cursor: pointer;
  background-color: #fff;
  background-color: var(--primary);
}

.btn--primary {
  background-color: #fff;
  background-color: var(--primary);
  color: #242424;
  border: 1px solid #fff;
  border: 1px solid var(--primary);
}

.btn--outline {
  background-color: transparent;
  color: #fff;
  padding: 1px 5px;
  border: 1px solid #fff;
  border: 1px solid var(--primary);
  transition: all 0.3s ease-out;
}

.btn--medium {
  padding: 1px 5px;
  font-size: 14px;
}

.btn--large {
  padding: 12px 25px;
  font-size: 16px;
}

.btn--large:hover,
.btn--medium:hover {
  transition: all 0.3s ease-out;
  background: #009387;
  color: #242424;
  transition: 250ms;
}
