.wrappermenu{
  display: inline-flex;
}
.wrappermenu .icon{
  margin: 0 20px;
  text-align: center;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
  z-index: 2;
  transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.wrappermenu .icon span{
  display: block;
  height: 60px;
  width: 60px;
  background: #fff;
  border-radius: 50%;
  position: relative;
  z-index: 2;
  box-shadow: 0px 10px 10px rgba(0,0,0,0.1);
  transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.wrappermenu .icon span i{
  line-height: 60px;
  font-size: 25px;
}
.wrappermenu .icon .tooltip{
  position: absolute;
  top: 0;
  z-index: 1;
  background: #fff;
  color: #fff;
  padding: 10px 18px;
  font-size: 17px;
  font-weight: 500;
  border-radius: 25px;
  opacity: 0;
  pointer-events: none;
  box-shadow: 0px 10px 10px rgba(0,0,0,0.1);
  transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.wrappermenu .icon:hover .tooltip{
  top: 70px;
  opacity: 1;
  pointer-events: auto;
}
.icon .tooltip:before{
  position: absolute;
  content: "";
  height: 15px;
  width: 15px;
  background: #fff;
  left: 50%;
  bottom: 80%;
  transform: translateX(-50%) rotate(45deg);
  transition: 0.4s cubic-bezier(0.68, -0.55, 0.265, 1.55);
}
.wrappermenu .icon:hover span{
  color: #fff;
}
.wrappermenu .icon:hover span,
.wrappermenu .icon:hover .tooltip{
  text-shadow: 0px -1px 0px rgba(0,0,0,0.4);
}
.wrappermenu .menu:hover span,
.wrappermenu .menu:hover .tooltip,
.wrappermenu .menu:hover .tooltip:before{
  background:  #FF914D;
}
.wrappermenu .twitter:hover span,
.wrappermenu .twitter:hover .tooltip,
.wrappermenu .twitter:hover .tooltip:before{
  background: #46C1F6;
}


