.maumenu{
  background-color: #00786E;
  width: 100%;
}
.menucongcu{
  height: 10%;
  max-width: 1200px;
  width: 100%;
  margin:0 auto;
  display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: space-between;
	align-items: stretch;
	align-content: stretch;
  background: linear-gradient(90deg,  #009387 0%, #009387 100%);
}

.congcutrai{
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: flex-start; 
	align-items: stretch;
	align-content: stretch;
}
.congcuphai{
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: flex-end;
	align-items: stretch;
	align-content: stretch;
}
.congcu {
    font-size: 15px;
    color: white;
    display: inline-block;
    padding: 0 0.5rem 0 0.5rem;
    text-decoration: none;
    background-image: linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255));
    background-size: 0 4px, auto;
    background-repeat: no-repeat;
    background-position: center bottom;
    transition: all .2s ease-out;
    text-align: center;
}

.congcu:hover {
  background-size: 100% 4px, auto;
}
.buttoncongcu {
    
    font-size: 15px;
    color: white;
    display: inline-block;
    padding: 1rem;
    text-decoration: none;
    background-image: linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255));
    background-size: 0 4px, auto;
    background-repeat: no-repeat;
    background-position: center bottom;
    transition: all .2s ease-out;
    text-align: center;
}
.congcuan {
    
    font-size: 15px;
    color: white;
    display: inline-block !important;
    padding: 0.5rem !important;
    text-decoration: none;
    background-image: linear-gradient(rgb(255, 255, 255), rgb(255, 255, 255));
    background-size: 0 4px, auto;
    background-repeat: no-repeat;
    background-position: center bottom;
    transition: all .2s ease-out;
    text-align: center ;
    border-radius: 0% !important;
}

.congcuan:hover {
  display: block;
  background-size: 100% 4px, auto;
}
.navbar {
  height: 100%;
  background: linear-gradient(90deg,  #009387 0%, #009387 100%);
  /* display: flex; */
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}
.containermenu{
  justify-content: center;
  align-items: center;
    max-width: 1200px;
    width: 100%;
    height:90%;
    /* border: 5px solid gray; */
    display: flex;
    margin: 0 auto;
}
.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  width: 100%;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
}
.header__logo{
  display: flex-start;
  flex:1;
  height: 50px;
    object-fit: contain;
    margin-right: 20px;
    margin-left: 10px;
}
.header__logomobile{
  display: none;
  flex:1;
  height: 50px;
    object-fit: contain;
    margin-right: 20px;
    margin-left: 10px;
}
.fa-mug-hot {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}


.header_search{
       flex: 1 1;
    width: 100%;
    margin-right: 5px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-end;
    align-items: stretch;
    align-content: stretch;

}

.header_searchInput{
    height: 30px;
    width:10%;
    border:none;
    
}

.header_searchIcon{
    padding: 5px;
    height: 30px !important;
    background-color: #804826;
    border: none;
    color: white;
    text-align: center;
    font-size: 16px;
    margin-left: 6px;
    opacity: 0.6;
    transition: 0.3s;
    display: inline-block;
    text-decoration: none;
    cursor: pointer;
}
.header_searchIcon:hover {opacity: 1}

.fa-caret-down {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(6, auto);
  /* grid-gap:0.5rem; */
  list-style: none;
  text-align: center;
  justify-content: end;
    display: inline-flex;
}
.nav-menucongcu {
 width: 100%;

}

.nav-item {
  height: 80px;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 0.5rem;
  height: 100%;
}

/* .nav-links:hover {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
} */

.nav-linksmini {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 0.5rem;
  height: 100%;
}

