  color: #fff;
}

.nav-links-mobile {
  display: none;
}

.nav-linksmini-mobile {
  display: none;
}

.menu-icon {
  display: none;
}

.menu-bars1 {
  display: none;
}

.navbar1 {
    height: 70px;
    display: none;
    justify-content: start;
    align-items: center;
    padding: 2rem 2rem;
    justify-content: center;
    margin-left: 30px;
    
}

.menu-bars1 {
  margin-left: 1.5rem;
  font-size: 2rem;
  background: none;
}

.nav-menu1  {
  background-color: #000000;
  width: 250px;
  height: 100vh;
  display: flex ;
  justify-content: center;
  position: fixed;
  top: 0;
  left: -100%;
  transition: 850ms;
}

.nav-menu1.active {
  left: 0;
  transition: 350ms;
}

.nav-text1 {
  display: flex;
  justify-content: start;
  align-items: center;
  padding: 8px 0px 8px 16px;
  list-style: none;
  height: 60px;
}

.nav-text1 a {
  text-decoration: none;
  color: #f5f5f5;
  font-size: 18px;
  width: 95%;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 0 16px;
  border-radius: 4px;
}

.nav-text1 span {
margin-right: 15px;
}

.nav-text1 a:hover {
  background: linear-gradient(90deg,  #FF914D 0%, #FF914D 100%);
}

.nav-menu1-items {
  width: 100%;
}

.navbar-toggle1 {
  background-color: #000000;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: start;
  align-items: center;
}






